body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

main {
  font-weight: 330;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

#mainNav {
  overflow: hidden;
  min-height: 100vh;
  height: 100vh;
}

#content {
  min-height: 100vh;
}

@media only screen and (min-width: 992px) {
  #content {
    height: 100vh;
    overflow: auto;
  }
}

@media only screen and (max-width: 991px) {
  body {
    overflow: auto; 
  }

  #mainNav {
    height: auto;
    overflow: hidden;
  }

  #content {
    height: auto;
    /*overflow: hidden;*/
  }
}

.Container {
  width: 100%;
}

.App-link {
  color: #61dafb;
}

.section-enter, .test-appear{
  opacity: 0;
}

.section-enter-active, .test-appear-active{
  opacity: 1;
  transition: opacity 1000ms;
}

.section-enter-done{
  opacity: 1;
}

.section-exit{
  opacity: 1;
}

.section-exit-active{
  opacity: 0;
  transition: opacity 500ms;
}

.section-exit-done{
  opacity: 0;
}

.nav-btn:hover {
  font-weight: bold;
  text-shadow: 0px 0px 15px #ccc;
}